:root {
  --back_1: #F6F5F5;
  --back_2: #D3E0E3;
  --back_top: white;
  --back_bottom: black;
  --font_1: #1687A7;
  --font_2: #276678;
}

.App_wrap {
  width: 100%;
}

.App_main {
  /* border: 2px solid black; */
  max-width: 900px;
  margin: auto;
}

@media (max-width: 1000px) {
  .App_side {
    display: flex;
    flex-direction: row;
    justify-content: space-around;

    background-color: var(--back_2);
    /* width: calc(80% - 10px); */
    padding: 10px;

  }
}

@media (min-width: 1000px) {
  .App_main {
    display: flex;
    flex-direction: row-reverse;
    max-width: 1100px;
  }

  .App_side {
    display: flex;
    flex-direction: column;
    justify-content: normal;
  }
}

@media (min-width: 1100px) {
  .App_main {
    display: flex;
    flex-direction: row-reverse;
    max-width: 1200px;
    justify-content: space-between;
  }

  .App_side {
    display: flex;
    flex-direction: column;
    justify-content: normal;
    margin: 0 50px;
  }
}

.App_top {
  background-color: var(--back_top);
  width: calc(100% - 20px);
  padding: 10px;
  margin: auto;
}

.App_top>h1 {
  /* background-color: red; */
  text-align: center;
  width: calc(80% - 20px);
  padding: 10px;
  margin: 10px auto;
}

.App_bottom {
  background-color: var(--back_bottom);
  color: white;
  width: calc(100% - 20px);
  padding: 10px;
  text-align: center;
}

.App_part1 {
  background-color: var(--back_1);
  width: calc(80% - 10px);
  padding: 10px 20% 10px 10px;
}

.App_part2 {
  background-color: var(--back_2);
  width: calc(80% - 10px);
  padding: 10px 10px 10px 20%;
}

.App_part1>h2 {
  color: var(--font_1);
}

.App_part2>h2 {
  color: var(--font_2);
}

.App_part1>h2, .App_part2>h2 {
  padding: 0px;
  /* background-color: red; */
  margin: 10px auto;
  width: calc(80% - 0px);
}

.App_main_p {
  padding: 10px 0;
  /* background-color: red; */
  margin: 0px auto;
  width: calc(80% - 0px);
  font-size: 18px;
  font-weight: 500;
}

@media (max-width: 510px) {
  .App_part1 {
    background-color: var(--back_1);
    width: calc(100% - 10px);
    padding: 15px 5px;
  }

  .App_part2 {
    background-color: var(--back_2);
    width: calc(100% - 10px);
    padding: 15px 5px;
  }

  .App_top>h1 {
    /* background-color: red; */
    text-align: center;
    width: 100%;
    padding: 10px 0px;
    margin: 10px auto;
  }

  .App_part1>h2, .App_part2>h2 {
    padding: 0px;
    /* background-color: red; */
    margin: 0px auto;
    width: 100%;
    text-align: center;
  }

  .App_main_p {
    padding: 0px;
    /* background-color: red; */
    margin: 10px auto;
    width: 90%;
    /* text-align: center; */
    font-size: 16px;
    font-weight: 500;
  }



}

@media (max-width: 400px) {
  .App_main_p {
    padding: 0px;
    /* background-color: red; */
    margin: 10px auto;
    width: 90%;
    /* text-align: center; */
    font-size: 14px;
    font-weight: 500;
  }
}


